<template lang="pug">
.page-container
    .page-title 假期管理
    el-row(:gutter="20")
        el-col(:span="12")
            el-table.list-container(:data="holiday")
                el-table-column(prop="date" label="日期")
                    template(#default='scope')
                        div {{getDateStr(scope.row.date)}}
                el-table-column(prop="discription" label="描述")
                el-table-column(label="功能按鈕")
                    template(v-slot:default="slotProps") 
                        el-button(type="danger" size="small" @click="clickDelete(slotProps.row.id,slotProps.$index)") 刪除
        el-col(:span="12")
            el-form(:model="form" :rules="rules" ref="form")
                el-form-item(label="日期" prop="date")
                    el-date-picker.input-container(
                        v-model="form.date"
                        type="date"
                        placeholder="選擇日期")
                el-form-item(label="描述" prop="discription")
                    el-input.input-container(placeholder="請輸入描述"  v-model="form.discription")
            el-button(type="warning" @click="addHoliday" style="margin-left: 5px;") 新增
</template>
<script>
export default {
  data() {
    return {
        form: {
            date: '',
            discription: ''
        },
        rules: {
            date: [
                { required: true, message: '請選擇日期', trigger: 'blur' }
            ],
            discription: [
                { required: true, message: '請輸入描述', trigger: 'blur' }
            ],
        },
        holiday: []
    }
  },
  created() {
    this.holiday = this.$store.state.holiday.data
  },
  methods: {
    addHoliday() {
        this.$http.post("/api/holiday",{
            date: this.getDateStr(this.form.date),
            discription: this.form.discription
        })
        .then(response => {
            console.log(response.data.message)
            this.holiday.push({date:this.form.date, discription:this.form.discription})
            this.$refs['form'].resetFields()
            this.$message({
                type: 'success',
                message: '新增成功!'
            })
        })
        .catch((error) => {
            //發送請求失敗時
            console.log(error)
        });
    },
    clickDelete(id, tableIdx) {
        this.$http.delete("/api/holiday/"+id)
        .then(response => {
            console.log(response.data.message)
            this.$message({
                type: 'success',
                message: '刪除成功!'
            });
            console.log(tableIdx)
            this.holiday.splice(tableIdx,1)
        })
        .catch((error) => {
            //發送請求失敗時
            console.log(error)
            this.$message({
                type: 'error',
                message: '刪除失敗，請重新載入此網頁'
            });
        });
    },
    getDateStr(date){
        var today = new Date(date)
        return today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()
    },
  }
}
</script>
<style scoped lang="sass">
@import "@/style/common.sass"
.is-selected
  color: #1989fa
</style>